<template>
    <a id="example6" class="selector" placeholder="userId" @click="select"></a>
</template>
<script>
import myPicker from 'my-picker'
var picker = new myPicker({
    cols: {
        options:[{
            userId:1,
            name:"(userId:1)"
        },{
            userId:2,
            name:"(userId:2)"
        },{
            userId:3,
            name:"(userId:3)"
        },{
            userId:4,
            name:"(userId:4)"
        },],
        labelKey: 'name',
        valueKey: 'userId',
    },
    title:"userId",
    onOkClick: function (values) {
        document.querySelector('#example6').textContent = 'userId' + values[0];
    },
})
export default {
    methods: {
        select(){
            picker.show();
        },
    }
}
</script>



