<template>
    <a id="example3" class="selector" @click="select" placeholder=""></a>
</template>
<script>
import myPicker from 'my-picker'
var picker = new myPicker({
    cols: [{
        options:["", "", "", "", "", ""],
        suffix: '',
    },{
        options:['1', '2', '3', '4', '5'],
        suffix: '',
    }],
    title:"",
    onOkClick: function (values) {
        document.querySelector('#example3').textContent = (values[0] + "" + values[1] + "");
    },
})
export default {
    methods: {
        select(){
            picker.show();
        }
    }
}
</script>



