<template>
    <Doc>
        <P><A href="#" @click.native.self.prevent="show">pickerpicker</A></P>
        <P><A href="#" @click.native.self.prevent="close">picker</A></P>
    </Doc>
</template>
<script>
import myPicker from 'my-picker'

export default {
    methods: {
        show(){
            try{
                this.picker.show()
                setTimeout(()=>{
                    this.picker.hide()
                }, 2000)
            } catch(e){
                alert('picker')
            }
        },
        close(){
            try{
                this.picker.close()
                alert('picker')
            } catch(e){
                alert('picker')
            }
        },
    },
    created(){
        this.picker = new myPicker({
            cols: ['', '', '', '', '', '', '', ''],
        })
    }
}
</script>


