<template>
    <a id="example2" class="selector" @click="select" placeholder=""></a>
</template>
<script>
import myPicker from 'my-picker'
var picker = new myPicker({
    cols: [{
        prefix: '',
        options: ['', '', '', '', ''],
        suffix: '',
    }],
    title:"",
    onOkClick: function (values) {
        document.querySelector('#example2').textContent = '' + (values[0]) + '';
    },
})
export default {
    methods: {
        select(){
            picker.show();
        }
    }
}
</script>



