<template>
    <Doc>
        <H1>my-picker</H1>
        <P>API<A href="#/API">API</A></P>

        <H2></H2>
        <P>IOSPickerView3D</P>
        <Demo :demo="example1" :code="code1" title=""></Demo>
        
        <H2></H2>
        <P></P>
        <Demo :demo="example2" :code="code2" title=""></Demo>
        
        <H2></H2>
        <P>3d3</P>
        <Demo :demo="example3" :code="code3" title=""></Demo>
        
        <H2></H2>
        <P>apiapipickerapi</P>
        <Demo :demo="example4" :code="code4" title=""></Demo>
        
        <H2></H2>
        <P>api</P>
        <Demo :demo="example5" :code="code5" title=""></Demo>

        <H2>labelvalue</H2>
        <P>pickerjsonjsonlabelvaluekey</P>
        <Demo :demo="example6" :code="code6" title="labelvalue"></Demo>
        
        <H2></H2>
        <P>api</P>
        <Demo :demo="example7" :code="code7" title=""></Demo>
        
        <H2></H2>
        <P>picker</P>
        <Demo :demo="example8" :code="code8" title=""></Demo>
        
        <H2>3D</H2>
        <P><Strong>myPicker</Strong>3Dpickerpickercss3<Strong>transform-style: preserve-3d</Strong></P>
        <Demo :demo="example9" :code="code9" title="3D"></Demo>
        
        <H2></H2>
        <P><Strong>myPicker</Strong><Strong>hasVoice</Strong></P>
        <Demo :demo="example10" :code="code10" title="">
            <P>picker</P>
        </Demo>
        
        <H2></H2>
        <P><Strong>myPicker</Strong>IOSpicker<Strong>hasGlassCover</Strong></P>
        <Demo :demo="example11" :code="code11" title="">
            <P>picker</P>
        </Demo>

        <Comments></Comments>

    </Doc>
</template>
<script>
import example1 from '../demo/Useage/example1.vue'
import example2 from '../demo/Useage/example2.vue'
import example3 from '../demo/Useage/example3.vue'
import example4 from '../demo/Useage/example4.vue'
import example5 from '../demo/Useage/example5.vue'
import example6 from '../demo/Useage/example6.vue'
import example7 from '../demo/Useage/example7.vue'
import example8 from '../demo/Useage/example8.vue'
import example9 from '../demo/Useage/example9.vue'
import example10 from '../demo/Useage/example10.vue'
import example11 from '../demo/Useage/example11.vue'
export default {
    data(){
        return {
            example1,
            example2,
            example3,
            example4,
            example5,
            example6,
            example7,
            example8,
            example9,
            example10,
            example11,
            code1:
`<a id="example1" class="selector" placeholder=""></a>
<script>
    var picker1 = myPicker({
        cols: ['java','c#','JavaScript','php','Python'],
        title:"",
        onOkClick: function (values) {
            document.querySelector('#example1').textContent = (values[0]);
        },
    })
    document.querySelector('#example1').addEventListener('click', function () {
        picker1.show();
    })
<\/script>`,
            code2:
`<a id="example2" class="selector" @click="select" placeholder=""></a>
<script>
    var picker2 = myPicker({
        cols: [{
            prefix: '',
            options: ['', '', '', '', ''],
            suffix: '',
        }],
        title:"",
        onOkClick: function (values) {
            document.querySelector('#example2').textContent = '' + (values[0]) + '';
        },
    })
    document.querySelector('#example2').addEventListener('click', function () {
        picker2.show();
    })
<\/script>`,
            code3:
`<a id="example3" class="selector" @click="select" placeholder=""></a>
<script>
    var picker3 = myPicker({
        cols: [{
            options:["", "", "", "", "", ""],
            suffix: '',
        },{
            options:['1', '2', '3', '4', '5'],
            suffix: '',
        }],
        title:"",
        onOkClick: function (values) {
            document.querySelector('#example3').textContent = (values[0] + "" + values[1] + "");
        },
    })
    document.querySelector('#example3').addEventListener('click', function () {
        picker3.show();
    })
<\/script>`,
            code4:
`<a id="example4_1" class="selector">2</a>
<a id="example4_2" class="selector">2</a>
<script>
    var picker4_1 = myPicker({
        cols: ['1', '2', '3', '4', '5'],
        setValues: [2],
        onOkClick: function (values) {
            document.querySelector('#example4_1').textContent = values[0];
            document.querySelector('#example4_2').textContent = values[0];
            picker4_2.setValue(0, values[0]);
        },
    })
    document.querySelector('#example4_1').addEventListener('click', function () {
        picker4_1.show();
    })
    var picker4_2 = myPicker({
        cols: ['1', '2', '3', '4', '5'],
        setValues: [2],
        onOkClick: function (values) {
            document.querySelector('#example4_1').textContent = values[0];
            document.querySelector('#example4_2').textContent = values[0];
            picker4_1.setValue(0, values[0]);
        },
    })
    document.querySelector('#example4_2').addEventListener('click', function () {
        picker4_2.show();
    })

<\/script>`,
            code5:
`<a id="example5" class="selector" placeholder=""></a>
<script>
    var picker5 = myPicker({
        cols: [
            ['A0', 'B0', 'C0'],
            ['D0', 'E0', 'F0']
        ],
        onSelectItem: function (level, selectedIndex, selectedValue) {
            //
            var options = this.getOptions(level);

            //,picker
            var count = /(\d+)/.exec(options[selectedIndex])[0];
            options[selectedIndex] = options[selectedIndex].replace(count, parseInt(count) + 1 + "");

            //,
            this.setOptions(level, options, options[selectedIndex]);
        },
        onOkClick: function (values) {
            document.querySelector('#example5').textContent = values[0] + " " + values[1];
        },
    })
    document.querySelector('#example5').addEventListener('click', function () {
        picker5.show();
    })
<\/script>`,
            code6:
`<a id="example6" class="selector" placeholder="userId"></a>
<script>
    var picker6 = myPicker({
        cols: {
            options:[{
                userId:1,
                name:"(userId:1)"
            },{
                userId:2,
                name:"(userId:2)"
            },{
                userId:3,
                name:"(userId:3)"
            },{
                userId:4,
                name:"(userId:4)"
            },],
            labelKey: 'name',
            valueKey: 'userId',
        },
        title:"userId",
        onOkClick: function (values) {
            document.querySelector('#example6').textContent = 'userId' + values[0];
        },
    })
    document.querySelector('#example6').addEventListener('click', function () {
        picker6.show();
    })
<\/script>`,
            code7:
`<a id="example7" class="selector" placeholder=""></a>
<script>
    var athlete = [{
        name:"",
        athlete: ['','c','','']
    },{
        name:"",
        athlete: ['','','']
    }]
    var picker7 = myPicker({
        cols: [
            {
                options: athlete,
                labelKey: 'name',
                valueKey: 'name',
            },
            //,
            athlete[0].athlete
        ],
        setValues: ['',''],
        onSelectItem: function (level, selectedIndex, selectedValue) {
            if(level == 0){
            this.setOptions(1, athlete[selectedIndex].athlete);
            }

        },
        title:"",
        onOkClick: function (values) {
            document.querySelector('#example7').textContent = ':' + values[1];
        },
    })
    document.querySelector('#example7').addEventListener('click', function () {
        picker7.show();
    })
<\/script>`,
            code8:
`<a id="example8" class="selector" placeholder=""></a>
<script>
    var year = [];
    for(var i = 16; i < 30 ; i++){
        year.push(i + 2000);
    }

    var month = [];
    for(var i = 1; i <= 12 ; i++){
        month.push(i);
    }

    var today = new Date();

    var picker8  = myPicker({
        cols : [{
            options: year,
            suffix: "",
        },{
            options: month,
            suffix: "",
        },{
            options: [],
            suffix: "",
        },],
        onOkClick: function (values) {
            document.querySelector('#example8').textContent = values[0] + "" + values[1] + "" + values[2] + "";
        },
        setValues: [today.getFullYear(), today.getMonth() + 1, today.getDate()],
        onSelectItem : function (i, index, value) {

            if(i != 2){
            var year = this.getValue(0);
            var month = this.getValue(1);

            if(year == null || month == null)
                return

            var curDate = new Date();
            curDate.setYear(year)
            curDate.setMonth(month);
            curDate.setDate(0);

            var day = [];
            for(var i = 1; i <= curDate.getDate() ; i++){
                day.push(i);
            }
            this.setOptions(2, day);
            }
        }
    });
    document.querySelector('#example8').addEventListener('click', function () {
        picker8.show();
    })
<\/script>`,
            code9:
`<a id="example9" class="selector" placeholder=""></a>
<script>
    var year = [];
    for(var i = 16; i < 30 ; i++){
        year.push(i + 2000);
    }

    var month = [];
    for(var i = 1; i <= 12 ; i++){
        month.push(i);
    }

    var today = new Date();

    var picker9  = myPicker({
        cols : [{
            options: year,
            suffix: "",
        },{
            options: month,
            suffix: "",
        },{
            options: [],
            suffix: "",
        },],
        onOkClick: function (values) {
            document.querySelector('#example8').textContent = values[0] + "" + values[1] + "" + values[2] + "";
        },
        setValues: [today.getFullYear(), today.getMonth() + 1, today.getDate()],
        onSelectItem : function (i, index, value) {

            if(i != 2){
            var year = this.getValue(0);
            var month = this.getValue(1);

            if(year == null || month == null)
                return

            var curDate = new Date();
            curDate.setYear(year)
            curDate.setMonth(month);
            curDate.setDate(0);

            var day = [];
            for(var i = 1; i <= curDate.getDate() ; i++){
                day.push(i);
            }
            this.setOptions(2, day);
            }
        }
    });
    document.querySelector('#example9').addEventListener('click', function () {
        picker9.show();
    })
<\/script>`,
            code10:
`<a id="example10" class="selector" placeholder=""></a>
<script>
    var picker10 = myPicker({
        cols: ['java','c#','JavaScript','php','Python'],
        title:"",
        hasVoice: false,
        onOkClick: function (values) {
            document.querySelector('#example10').textContent = (values[0]);
        },
    })
    document.querySelector('#example10').addEventListener('click', function () {
        picker10.show();
    })
<\/script>`,
            code11:
`<a id="example11" class="selector" placeholder=""></a>
<script>
    var picker11 = myPicker({
        cols: ['java','c#','JavaScript','php','Python'],
        title:"",
        hasGlassCover: false,
        onOkClick: function (values) {
            document.querySelector('#example11').textContent = (values[0]);
        },
    })
    document.querySelector('#example11').addEventListener('click', function () {
        picker11.show();
    })
<\/script>`,
        }
    }
}
</script>
<style>

.selector{
    margin-bottom: 1rem;
    background-color: rgba(255, 255, 255, 0.08);
    border-color: rgba(0, 0, 0, 0.2);
    border-style: solid;
    border-width: 1px;
    border-radius: 0.3rem;
    transition: color 0.2s, background-color 0.2s, border-color 0.2s;
    padding: 10px;
    width: auto;
    display: block;
    cursor: pointer;
    position: relative;
  }
  .selector:empty::before{
    color: #999;
    content: attr(placeholder);
  }
  .selector::after{
    position: absolute;
    right: 10px;
    top:14px;
    width: 10px;
    height: 10px;
    content: "";
    display: block;
    border: solid;
    border-width: 0 0 2px 2px;
    border-color: #000;
    transform: rotate(-45deg);
  }
  .selector:hover {
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.2);
    border-color: rgba(0, 0, 0, 0.3);
  }
  
</style>



