<template>
    <a id="example7" class="selector" placeholder="" @click="select"></a>
</template>
<script>
import myPicker from 'my-picker'

var athlete = [{
    name:"",
    athlete: ['','c','','']
},{
    name:"",
    athlete: ['','','']
}]

var picker = new myPicker({
    cols: [
        {
            options: athlete,
            labelKey: 'name',
            valueKey: 'name',
        },
        //,
        athlete[0].athlete
    ],
    setValues: ['',''],
    onSelectItem: function (level, selectedIndex, selectedValue) {
        if(level == 0){
            this.setOptions(1, athlete[selectedIndex].athlete);
        }

    },
    title:"",
    onOkClick: function (values) {
        document.querySelector('#example7').textContent = ':' + values[1];
    },
})
export default {
    methods: {
        select(){
            picker.show();
        },
    }
}
</script>



