<template>
    <A href="#" @click.native.self.prevent="select"></A>
</template>
<script>
import myPicker from 'my-picker'

export default {
    methods: {
        select(){
            (new myPicker({cols: ['', '', '', '', '', '', '', '']})).show()
        }
    }
}
</script>


