<template>
    <A href="#" @click.native.self.prevent="select"></A>
</template>
<script>
import myPicker from 'my-picker'

export default {
    methods: {
        select(){
            (new myPicker({
                cols: [
                    ['', '', '', '', ''],
                    ['I', 'II', 'III', 'IV', 'V'],
                ],
                onSelectItem(level, selectedIndex, selectedValue){
                    alert(`${level}${selectedIndex}${selectedValue}`)
                }
            })).show()
        }
    }
}
</script>


